import React, { useEffect, useState, useRef } from 'react'
import Input from "../input/Input";
import PhoneField from "../phone/PhoneInput";
import LongInput from "../long-input/Long-input";
import Spinner from "../../../components/spinner/Spinner";
import Button from "../button/Button";
import { useTranslation } from "react-i18next";
import { useHttp } from "../../../hooks/hook.http";
import TermsComponent from './terms-component';
import { validateEmail } from '../../../utils/validate-utils';
import phone from 'phone'
import { useErrors } from '../../../hooks/errors-hook';

import './casting-form.scss'
import '../../../components/be-free/components/casting/Casting.scss'
import '../../../components/be-free/Be-free.scss'
import '../../../components/be-free/components/join/JoinTheTeam.scss'
import './casting-textArea.scss'

const CastingForm = ({ setDone }) => {
    const [form, setForm] = useState({
        name: '',
        email: '',
        phone: '',
        video1: '',
        video2: '',
        about: '',
        surname: ''
    })
    const [canSend, setCanSend] = useState(false)
    const [isAgree, setIsAgree] = useState(false)
    const [termsModalIsOpen, setTermsModalIsOpen] = useState(false)

    const { t } = useTranslation()

    const { request, loading } = useHttp()
    const {
        arrayOfErrors,
        clearErrors,
        onSetError,
        onSetErrorsFromRequest,
        getErrorFieldByName
    } = useErrors()

    useEffect(() => {
        const { name, email, phone, video1, video2, about, surname } = form
        if (
            name.trim().length &&
            email.trim().length &&
            phone.length &&
            video1.trim().length &&
            video2.trim().length &&
            about.trim().length > 10 &&
            surname.trim().length &&
            isAgree
        ) {
            setCanSend(true)
        } else {
            setCanSend(false)
        }
    }, [form, isAgree])

    useEffect(() => {
        return () => clearErrors()
    }, [])

    const onToggleTermsModal = () => setTermsModalIsOpen(!termsModalIsOpen)

    const sendForm = async () => {
        try {
            clearErrors()
            if (phone(form.phone, '').length > 0) {
                if (validateEmail(form.email)) {
                    const formData = new FormData()
                    formData.append('name', form.name)
                    formData.append('surname', form.surname)
                    formData.append('email', form.email)
                    formData.append('phone', form.phone)
                    formData.append('video1', form.video1)
                    formData.append('video2', form.video2)
                    formData.append('about', form.about)
                    const response = await request(
                        '/api/casting/new',
                        'POST',
                        formData
                    )
                    if (response.status) {
                        clearErrors()
                        setDone(true)
                        setTimeout(() => setDone(false), 3000)
                    }
                } else {
                    onSetError('email', 'Email is not valid!')
                }
            } else {
                onSetError('phone', 'Phone is not valid!')
            }
        } catch (err) {
            const resErrors = err.response.data.errors
            onSetErrorsFromRequest(resErrors)
        }
    }

    const toggleIsAgree = () => setIsAgree(!isAgree)

    const changeHandler = e => setForm({ ...form, [e.target.name]: e.target.value })

    const phoneChange = value => setForm({ ...form, phone: value })

    return (
        <>
            <div className="error_wrapper">
                {arrayOfErrors.map((error, idx) =>
                    <p key={idx} className="input_error error_visible">{error}</p>
                )}
            </div>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                       
            <div className="casting_form-wrapper">
                <TermsComponent
                    termsModalIsOpen={termsModalIsOpen}
                    onToggleTermsModal={onToggleTermsModal}
                />
                <div className="befree-col casting__c-1">
                    <Input
                        tooltip={t('casting.tooltip.name')}
                        value={form.name}
                        error={getErrorFieldByName('name')}
                        name="name"
                        placeholder={t('name')}
                        changeHandler={changeHandler}
                    />
                    <Input
                        tooltip={t('casting.tooltip.surname')}
                        value={form.surname}
                        error={getErrorFieldByName('surname')}
                        name="surname"
                        placeholder={t('surname')}
                        changeHandler={changeHandler}
                    />
                    <Input
                        tooltip={t('casting.tooltip.email')}
                        value={form.email}
                        error={getErrorFieldByName('email')}
                        name="email"
                        placeholder={t('email')}
                        changeHandler={changeHandler}
                    />
                    <PhoneField
                        placeholder={t('phone')}
                        changeHandler={phoneChange}
                        phoneValue={form.phone}
                    />
                </div>
                <div className="befree-col casting__c-2">
                    <LongInput
                        tooltip={t('casting.tooltip.link.1')}
                        value={form.video1}
                        error={getErrorFieldByName('video1')}
                        name="video1"
                        placeholder={t('link.video')}
                        changeHandler={changeHandler}
                    />
                    <LongInput
                        tooltip={t('casting.tooltip.link.2')}
                        value={form.video2}
                        error={getErrorFieldByName('video2')}
                        name="video2"
                        placeholder={t('link.video')}
                        changeHandler={changeHandler}
                    />
                    <div className="field_wrapper">
                        <label htmlFor={`input_${form.about}`} className={
                            form.about?.trim().length <= 0
                                ? "field_placeholder visible"
                                : "field_placeholder"
                        }
                        >{t('transmittalLater')}</label>
                        <textarea
                            onChange={changeHandler}
                            className={
                                getErrorFieldByName('about')
                                    ? "long-input-casting input-error"
                                    : "long-input-casting"
                            }
                            name="about"
                            type="text"
                        />
                    </div>
                </div>
            </div>
            <div style={{ width: '70%', marginTop: '20px', marginBottom: '14px' }}>
                {t('casting.page.end')}
            </div>
            <div className="casting_terms">
                <input type="checkbox" className="custom-checkbox" id="accept" />
                <label onClick={toggleIsAgree} htmlFor="accept"> {t('casting.checkbox')} </label>
                <p
                    onClick={onToggleTermsModal}
                    className="casting_terms-modal-button"
                >
                    Terms of agreement
                </p>
            </div>
            {loading
                ? <Spinner />
                : <Button
                    onClick={sendForm}
                    disabled={!canSend}
                    title={t('send')}
                />
            }
        </>
    )
}

export default CastingForm
